import Hi from './actions/hi'
import Bye from './actions/bye'
import NotFound from './actions/404'
import welcome from './actions/welcome'
import name from './actions/name'
import scope from './actions/scope'
import workexperience from './actions/workexperience'
import ageAndContactNo  from './actions/ageAndContactNo'
import finalintent from './actions/finalintent'
import repeat from './actions/repeat'
import Businessfinal from './actions/businessfinal'

export const routes = [
  /* Captures different intents (enable the Dialogflow integration,
        see "integrations" section at the top of this file) */
  { path: 'welcome', intent: 'Default Welcome Intent', action: welcome },
  { path: 'name', intent: 'name', action: name },
  { path: 'scope', intent: 'scope', action: scope },      
  { path: 'workexperience', intent: 'workexperience', action: workexperience },
  { path: 'finalintent', intent: 'finalintent', action: finalintent },
  { path: 'ageAndContactNo', intent: 'ageAndContactNo', action:ageAndContactNo},
  { path: 'DefaultFallbackIntent', intent: 'Default Fallback Intent', action:welcome},
  { path: 'DefaultRepeatIntent', intent: 'Default Repeat Intent', action: repeat},
  { path: 'Business', intent: 'Business', action:welcome},
  { path: 'CompanyDetails', intent: 'Company Details', action: Businessfinal},
  { path: 'business_main_type', intent: 'business_main_type', action: welcome},
  { path: 'not_found', type: /.*/, action: NotFound }

  /* There's an implicit rule that captures any other input and maps it to
        the 404 action, it would be equivalent to:
        {type: /^.*$/, action: "404"}
        */
]
