import React from 'react'
import { Text, Reply, Button } from '@botonic/react'
export default class extends React.Component {

  static async botonicInit({ input, session, params, lastRoutePath }) {
		// console.log('quickreply id');
		// console.log(session);
		// console.log('this is input',input.dialogflowResponse.queryResult.fulfillmentText);
		// console.log('this is params',params);
    // console.log(lastRoutePath);
    return {'res': input.dialogflowResponse.queryResult.fulfillmentText, 'dailogflow':input.dialogflowResponse}
  }
	render() {
    // console.log(this.props.res);
    // console.log(this.props.dailogflow.queryResult.fulfillmentMessages);
    var textRes = this.props.dailogflow.queryResult.fulfillmentMessages[0].text['text'];
    const textres = [];
    const quickreplyres = [];
    const elements = this.props.dailogflow.queryResult.fulfillmentMessages;
    for (const [index, value] of elements.entries()) {
      if(value.text){
        textres.push(<Text key={index}>{value.text['text']}</Text>)
      }else if(value.quickReplies){
        var replyarr = value.quickReplies['quickReplies'];
        for (const [index, value] of replyarr.entries()) {
          quickreplyres.push(<Reply key={index} payload={value}>{value}</Reply>)
        }
        
      }
    
    };
    var tasdfsdff = textres[textres.length - 1].props.children.push(quickreplyres);
    // console.log('textres length', textres);

    // console.log('quickreplyres', quickreplyres)
    
    return   (
     
    <div>
    
      {textres}
    
</div>

    )
	}
}